<template>
  <div class="home">
    <b-container class="home-container">
      <div class="row justify-content-md-center">
        <b-col md="9">
      <div class="row">
        <h1>Denuncia</h1>
        <p>
          Colabora con esta plataforma para señalar los actos irregulares de campaña y/o reportar los eventos que observes a efecto
          de contabilizarlos como gastos de campaña.
        </p>
        <p>
          La información que proporciones será analizada a efecto de evaluar si se presentan quejas o denuncias en
          contra de cantidatos y/o partidos políticos.
        </p>
      </div>
          <b-form @submit.prevent="onSubmit">
            <b-row>
              <b-col>
                <b-form-group label="Candidato a denunciar:">
                  <b-form-input v-model="form.candidate" type="text" ></b-form-input>
                </b-form-group>
              </b-col>
            </b-row>
              <b-form-group class="position-fields">
                <b-form-radio v-model="form.position" name="position" v-for="position in catalogs.positions" :key="position.uuid" :value="position.id">{{position.name}}</b-form-radio>
              </b-form-group>
            </b-row>
            <b-row>
              <b-col md="6" sm="12" xs="12">
                <label>Estado:</label>
                <b-form-select v-model="form.state" @change="getCities">
                  <b-form-select-option v-for="state in catalogs.states" :key="state.uuid" :value="state.id">
                    {{state.name}}
                  </b-form-select-option>
                </b-form-select>
              </b-col>
              <b-col md="6" sm="12" xs="12">
                <label>Ciudad:</label>
                <b-form-select v-model="form.city">
                  <b-form-select-option v-for="city in cities" :key="city.uuid" :value="city.id">
                    {{city.name}}
                  </b-form-select-option>
                </b-form-select>
              </b-col>
            </b-row>
            <b-row>
              <b-col md="6" sm="12" xs="12">
                <label>Partido:</label>
                <b-form-select v-model="form.party">
                  <b-form-select-option v-for="party in catalogs.parties" :key="party.uuid" :value="party.code">
                    {{party.name}}
                  </b-form-select-option>
                </b-form-select>
              </b-col>
            </b-row>
            <b-row>
              <b-col>
                <label>Fecha del evento:</label>
                <b-form-datepicker v-model="form.eventDateTime" class="mb-2"></b-form-datepicker>
              </b-col>
            </b-row>
            <b-row>
              <b-col cols="12">
                <label>¿Qué quieres denunciar?</label>
                <b-form-textarea
                  v-model="form.description"
                  rows="3"
                  max-rows="6"
                ></b-form-textarea>
              </b-col>
              <b-col md="6" xs="12">
                <div class="p-2">
                  <p>
                    Esta es una plataforma privada, para cualquier comentario o información al respecto contactar a
                    <a href="mailto:jacinto.herrera@asacsc.com.mx">jacinto.herrera@asacsc.com.mx</a>
                  </p>
                  <p>
                    Si quieres que tu candidato utilice esta plataforma contáctanos.
                  </p>
                </div>
              </b-col>
              <b-col md="6" xs="12" class="complaint-files">
                <b-form-file multiple v-model="form.files" placeholder="Subir archivos">Subir pruebas</b-form-file>
              </b-col>
            </b-row>
            <b-row>
              <b-button class="primary-button" type="submit">Enviar</b-button>
            </b-row>
          </b-form>
        </b-col>
      </div>
    </b-container>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { createComplaint } from "@/services/complaints";
import { getCities } from "@/services/geo";

export default {
  name: "Home",
  data: () => ({
    cities: [],
    form: {
      candidate: "",
      campaign: "",
      position: null,
      party: null,
      eventDateTime: null,
      description: "",
      latitude: 0,
      longitude: 0,
      state: "",
      city: "",
    },
  }),
  methods: {
    ...mapActions({
      getStates: "catalogs/getStates",
      getParties: "catalogs/getParties",
      getPositions: "catalogs/getPositions",
      createComplaint: "complaints/create",
    }),
    async onSubmit() {
      try {
        const { data: response } = await createComplaint({
          candidate: this.form.candidate,
          campaign: this.form.campaign,
          position: this.form.position,
          party: this.form.party,
          eventDateTime: this.form.eventDateTime,
          description: this.form.description,
          latitude: this.form.latitude,
          longitude: this.form.longitude,
          state: this.form.state,
          city: this.form.city,
          files: this.form.files,
        });
        this.reset();
        this.$snack.success({
          text: "Tu reporte ha sido creado con éxito.",
        });
        if (this.auth.user) {
          this.$router.push("/admin/reportes");
        }
      } catch (err) {
        console.log(err);
      }
    },
    async getCities() {
      const { data: result } = await getCities(this.form.state);
      this.cities = result.data;
    },
    reset() {
      this.form.candidate = null;
      this.form.campaign = null;
      this.form.position = null;
      this.form.party = null;
      this.form.eventDateTime = null;
      this.form.description = null;
      this.form.latitude = 0;
      this.form.longitude = 0;
      this.form.state = null;
      this.form.city = null;
      this.form.files = [];
    },
  },
  computed: {
    ...mapState(["catalogs", "auth"]),
  },
  async mounted() {
    await this.getStates();
    await this.getParties();
    await this.getPositions();
  },
};
</script>

<style scoped>
.complaint-files {
  margin: 1em 0 !important;
}

.home-container {
  padding: 2em 0;
}
</style>
