<template>
  <modal name="login" class="modal--login" :adaptive="true" :scrollable="true" height="auto">
    <div class="row">
      <div class="col">
        <div class="p-3">
          <h3>Iniciar sesión</h3>
          <form @submit.prevent="onSubmit">
              <div class="p-1">
                <label>Correo electrónico:</label>
                <input
                  class="form-control"
                  v-model="form.email"
                  type="email"
                  placeholder="Correo electrónico"
                  required
                ></input>
              </div>
              <div class="p-1">
                <label>Contraseña</label>
                <input
                  class="form-control"
                  v-model="form.password"
                  type="password"
                  placeholder="Contraseña"
                  required
                ></input>
              </div>
              <div>
                <button class="btn btn-primary" type="submit">Iniciar sesión</button>
              </div>
          </form>
          <a href="#" @click.prevent="openRegisterModal">
            ¿No tienes una cuenta?
          </a>
        </div>
      </div>
    </div>
  </modal>
</template>

<script>
import { login, me } from '@/services/auth'

export default {
  data: function () {
      return {
        form: {
          email: "",
          password: ""
        },
      }
    },
  methods: {
    async onSubmit(e){
      try {
        const {data:response} = await login({email: this.form.email, password: this.form.password})
        this.$emit('login', response.data)
        this.form.email = '';
        this.form.password = '';
        const {data: meResponse} = await me()
        this.$emit('userData', meResponse.data)
      } catch (err) {
        this.$emit('error', err)
      }
    },
    openRegisterModal() {
      this.$emit('openRegisterModal')
    }
  }
}
</script>
