<template>
  <div>
  <b-navbar toggleable="lg" class="app-header">
    <b-container>
      <b-navbar-brand to="/">Reporte de gastos</b-navbar-brand>
      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav>
          <b-nav-item tag="div" v-if="role === 'ADMIN'">
            <router-link to="/admin/cuentas">Cuentas</router-link>
          </b-nav-item>
          <b-nav-item tag="div" v-if="role === 'CUSTOMER'">
            <router-link to="/admin/usuarios">Usuarios</router-link>
          </b-nav-item>
          <b-nav-item tag="div" v-if="['CUSTOMER', 'ADMIN'].includes(role)">
            <router-link to="/admin/reportes">Reportes</router-link>
          </b-nav-item>
        </b-navbar-nav>
        <b-navbar-nav class="ml-auto">
          <b-nav-item @click.prevent="openLoginModal" v-if="!user">Iniciar sesión</b-nav-item>
          <b-nav-item-dropdown v-if="user" text="Cuenta" right>
            <b-dropdown-item>
              <router-link to="/mis-datos">Mis datos</router-link>
            </b-dropdown-item>
            <b-dropdown-item @click.prevent="logout">
              Salir
            </b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>
      </b-collapse>
    </b-container>
  </b-navbar>
</div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  methods: {
    openLoginModal() {
      this.$emit('openLoginModal')
    },
    logout() {
      this.$emit('logout')
    }
  },
  computed: {
    ...mapState(['auth']),
    role() {
      try {
        return this.auth.user.role.code
      } catch {
        return null
      }
    },
    user() {
      try {
        return this.auth.user
      } catch {
        return null
      }
    }
  }
}
</script>

<style scoped lang="scss">
.user-link {
  padding: 0!important;
  .dropdown-item {
    display: block;
    padding: 10px 10px;
  }
}
</style>
