<template>
  <div id="scope">
    <div id="app">
      <Header @openLoginModal="showLoginModal" @logout="closeSession"></Header>
      <main id="main">
        <router-view/>
      </main>
      <Footer></Footer>
    </div>
    <Login @login="loginSuccess" @error="loginError" @openRegisterModal="showRegisterModal" @userData="onUserData"></Login>
    <Register @register="registerSuccess" @error="registerError" @openRegisterModal="showRegisterModal" @openLoginModal="showLoginModal" ></Register>
  </div>
</template>

<script>
import Header from '@/components/layout/Header.vue'
import Footer from '@/components/layout/Footer.vue'
import Login from '@/components/auth/Login.vue'
import Register from '@/components/auth/Register.vue'
import { mapActions } from 'vuex'
import {logout} from '@/services/auth'

export default {
  components: {
    Header,
    Footer,
    Login,
    Register
  },
  methods: {
    ...mapActions({
      authLogin: 'auth/login',
      authLogout: 'auth/logout',
      setUserData: 'auth/setUserData'
    }),
    showLoginModal() {
      this.$modal.show('login')
      this.$modal.hide('register')
    },
    loginSuccess(data) {
      this.authLogin(data)
      this.$modal.hide('login')
    },
    loginError(error) {
      console.log(error)
      this.$snack.danger({
        text: error.response.data.message
      })
    },
    async closeSession() {
      await logout();
      this.authLogout()
      this.$router.push('/')
    },
    showRegisterModal() {
      this.$modal.hide('login')
      this.$modal.show('register')
    },
    onUserData(data){
      this.setUserData(data)
    },
    registerSuccess(data){
      this.$modal.hide('register')
      this.$snack.success({
        text: "El registro fue exitoso"
      })
      this.$modal.show('login')
    },
    registerError(error){
      try {
        if(Array.isArray(error.response.data.message)) {
          this.$snack.danger({
            text: error.response.data.message[0]
          })
        } else {
          this.$snack.danger({
            text: error.response.data.message
          })
        }
      } catch (err) {
        this.$snack.danger({
          text: error.message
        })
      }
    }
  }
}
</script>
