import axios from "./axios";
import qs from "querystring";

export function createComplaint({
  candidate,
  campaign,
  position,
  party,
  eventDateTime,
  description,
  latitude,
  longitude,
  state,
  city
}) {
  return axios.post("/complaints", {
    candidate,
    campaign,
    position,
    party,
    eventDateTime,
    description,
    latitude,
    longitude,
    state,
    city
  });
}

export function updateComplaint(
  id,
  {
    candidate,
    campaign,
    position,
    party,
    eventDateTime,
    description,
    latitude,
    longitude,
    state,
    city
  }
) {
  return axios.patch(`/complaints/${id}`, {
    candidate,
    campaign,
    position,
    party,
    eventDateTime,
    description,
    latitude,
    longitude,
    state,
    city
  });
}

export function getComplaints(filter) {
  const query = qs.encode(filter);
  return axios.get("/complaints?" + query);
}

export function getComplaint(id) {
  return axios.get(`/complaints/${id}`);
}

export function deleteComplaint(id) {
  return axios.delete(`/complaints/${id}`);
}
