import { getStates } from "../services/geo";
import { getParties } from "../services/parties";
import { getPositions } from "../services/positions";

export default {
  namespaced: true,
  data: () => ({
    parties: [],
    positions: [],
    states: []
  }),
  actions: {
    async getPositions({ commit }, payload) {
      const { data: result } = await getPositions();
      commit("getPositions", result.data);
    },
    async getParties({ commit }, payload) {
      const { data: result } = await getParties();
      commit("getParties", result.data);
    },
    async getStates({ commit }) {
      const { data: result } = await getStates();
      commit("getStates", result.data);
    }
  },
  mutations: {
    getPositions(state, payload) {
      state.positions = payload;
    },
    getParties(state, payload) {
      state.parties = payload;
    },
    getStates(state, payload) {
      state.states = payload;
    }
  }
};
