<template>
  <modal name="register" class="modal--login" :adaptive="true" :scrollable="true" height="auto">
    <div class="row">
      <div class="col">
        <div class="p-3">
          <h3>Regístrate</h3>
          <form @submit.prevent="onSubmit">
            <div class="p-1">
                <label>Nombre *</label>
                <input
                  class="form-control"
                  v-model="form.name"
                  type="name"
                  required
                ></input>
              </div>
              <div class="p-1">
                <label>Correo electrónico *</label>
                <input
                  class="form-control"
                  v-model="form.email"
                  type="email"
                  required
                ></input>
              </div>
              <div class="p-1">
                <label>Teléfono</label>
                <input
                  class="form-control"
                  v-model="form.phone"
                  type="text"
                ></input>
              </div>
              <div class="p-1">
                <label>Contraseña *</label>
                <input
                  class="form-control"
                  v-model="form.password"
                  type="password"
                  required
                ></input>
              </div>
              <div>
                <label>Estado</label>
                <b-form-select v-model="form.state" @change="getCities">
                  <b-form-select-option v-for="state in catalogs.states" :key="state.uuid" :value="state.id">
                    {{state.name}}
                  </b-form-select-option>
                </b-form-select>
              </div>
              <div>
                <label>Ciudad</label>
                <b-form-select v-model="form.city">
                  <b-form-select-option v-for="city in cities" :key="city.uuid" :value="city.id">
                    {{city.name}}
                  </b-form-select-option>
                </b-form-select>
              </div>
              <div>
                <button class="btn btn-primary" type="submit">Enviar</button>
              </div>
          </form>
          <a href="#" @click.prevent="openLoginModal">
            ¿Ya tienes una cuenta?
          </a>
        </div>
      </div>
    </div>
  </modal>
</template>

<script>
import { register } from '@/services/auth'
import { mapState, mapActions } from 'vuex'
import { getCities } from '@/services/geo'

export default {
  data: function () {
    return {
      cities: [],
      form: {
        name: "",
        email: "",
        password: "",
        phone: null,
        city: null,
        state: null
      },
    }
  },
  computed: {
    ...mapState([
      'catalogs',
      'auth'
    ])
  },
  methods: {
    ...mapActions({
      'getStates': 'catalogs/getStates',
    }),
    async getCities() {
      const {data: result} = await getCities(this.form.state);
      this.cities = result.data;
    },
    async onSubmit(e){
      try {
        const {data: response} = await register({
          name: this.form.name,
          email: this.form.email,
          password: this.form.password,
          phone: this.form.phone,
          state: this.form.state,
          city: this.form.city
        })

        this.form.name = '';
        this.form.email = '';
        this.form.phone = '';
        this.form.password = '';
        this.form.city = null;
        this.form.state = null;
        this.$emit('register', response.data)
      }
      catch (err) {
        this.$emit('error', err)
      }
    },
    openLoginModal() {
      this.$emit('openLoginModal')
    }
  }
}
</script>
